require('./styles/index.scss');

document.addEventListener('DOMContentLoaded', function() {
  let infoCarousel = null;
  infoCarousel = M.Carousel.init($('.carousel.carousel-slider.info'), {
    fullWidth: true,
  });
  if (infoCarousel) {
    setInterval(() => {
      infoCarousel[0].next();
    }, 5000);
  }
  // init information collapsible
  const collapsibleElems = document.querySelectorAll('.collapsible');
  const collapsibleInstances = M.Collapsible.init(collapsibleElems, {
    accordion: false,
  });
  $('.btn-open-nav-menu').click(() => {
    if ($('.mobile-nav-list').hasClass('open')) {
      $('.mobile-nav-list').removeClass('open');
    } else {
      $('.mobile-nav-list').addClass('open');
    }
  });
  preloadImages();
});

function preloadImages() {
  const arr = [
    './images/Icon_logos.png',
    './images/Icon_headline.png',
    './images/Image_Info_1.png',
    './images/Image_Info_2.png',
    './images/Image_Info_3.png',
    './images/Image_Info_4.png',
    './images/Image_Info_5.png',
  ];
  const result = {};
  arr.forEach(url => {
    const image = new Image();
    image.onload = () => {
      result[url] = true;
      if (Object.keys(result).length === arr.length) finishMainLoading();
    };
    image.onerror = () => {
      result[url] = false;
      if (Object.keys(result).length === arr.length) finishMainLoading();
    };
    image.src = url;
  });
}

function finishMainLoading() {
  $('#main-loading').addClass('hide');
  $('.waiting-loading').removeClass('waiting-loading');
}
